.hero-slider-wrap {
  position: relative;
}

.hero-slider-wrap .owl-dots {
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: 30px;
}

@media (max-width: 991.98px) {
  .single-slider .owl-nav {
    display: none;
  }
}

.single-slider {
  margin-bottom: 90px;
}

.single-slider .owl-stage {
  overflow: hidden;
}

.single-slider .owl-prev,
.single-slider .owl-next {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 99;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.05) !important;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  transition: 0.3s all ease;
}

.single-slider .owl-prev:hover,
.single-slider .owl-next:hover {
  background: rgba(0, 0, 0, 0.1) !important;
}

.single-slider .owl-prev span,
.single-slider .owl-next span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.single-slider .owl-prev span:before,
.single-slider .owl-next span:before {
  font-size: 40px;
}

.single-slider .owl-nav .owl-prev,
.single-slider .owl-nav .owl-next {
  position: absolute;
  bottom: -70px;
  z-index: 99;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  color: #000 !important;
}

.single-slider .owl-nav .owl-prev span,
.single-slider .owl-nav .owl-next span {
  font-size: 30px;
}

.single-slider .owl-nav .owl-prev:hover,
.single-slider .owl-nav .owl-next:hover {
  color: #000;
}

.single-slider .owl-nav .owl-prev:active,
.single-slider .owl-nav .owl-prev:focus,
.single-slider .owl-nav .owl-next:active,
.single-slider .owl-nav .owl-next:focus {
  outline: none;
}

.single-slider .owl-nav .owl-prev {
  left: 60px;
}

.single-slider .owl-nav .owl-next {
  right: 60px;
}

.single-slider .owl-dots {
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: -50px;
}

.single-slider .owl-dots .owl-dot {
  display: inline-block;
}

.single-slider .owl-dots .owl-dot > span {
  margin: 4px;
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  transition: 0.3s all ease;
}

.single-slider .owl-dots .owl-dot.active > span {
  background: black;
}

.single-slider .owl-dots .owl-dot:active,
.single-slider .owl-dots .owl-dot:focus {
  outline: none;
}

.single-slider .owl-next {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 99;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.05) !important;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  transition: 0.3s all ease;
}

.cover-v1 {
  background-size: cover;
  background-position: center center;
  background-repeat: repeat;
  width: 100%;
}

.cover-v1.gradient-bottom-black {
  position: relative;
}

.cover-v1.gradient-bottom-black:before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  background: rgba(0, 0, 0, 0);
  background: -moz-linear-gradient(
    top,
    rgba(0, 0, 0, 0) 79%,
    rgba(0, 0, 0, 0.05) 80%,
    black 100%
  );
  background: -webkit-gradient(
    left top,
    left bottom,
    color-stop(79%, rgba(0, 0, 0, 0)),
    color-stop(80%, rgba(0, 0, 0, 0.05)),
    color-stop(100%, black)
  );
  background: -webkit-linear-gradient(
    top,
    rgba(0, 0, 0, 0) 79%,
    rgba(0, 0, 0, 0.05) 80%,
    black 100%
  );
  background: -o-linear-gradient(
    top,
    rgba(0, 0, 0, 0) 79%,
    rgba(0, 0, 0, 0.05) 80%,
    black 100%
  );
  background: -ms-linear-gradient(
    top,
    rgba(0, 0, 0, 0) 79%,
    rgba(0, 0, 0, 0.05) 80%,
    black 100%
  );
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(79%, rgba(0, 0, 0, 0)),
    color-stop(80%, rgba(0, 0, 0, 0.05)),
    to(black)
  );
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 79%,
    rgba(0, 0, 0, 0.05) 80%,
    black 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000', GradientType=0 );
}

.cover-v1.overlay {
  position: relative;
}

.cover-v1.overlay:before {
  z-index: 9;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  background: rgba(0, 0, 0, 0.15);
}

.cover-v1.overlay .mbYTP_wrapper {
  z-index: -1;
}

.cover-v1 .container {
  z-index: 9;
  position: relative;
}

.cover-v1 .container,
.cover-v1 .container > .row {
  height: 100vh;
  min-height: 650px;
}

.cover-v1 .heading {
  font-size: 5rem;
  font-weight: 900;
  color: #000;
}

.heading-span {
  color: #d63447;
}

@media (max-width: 991.98px) {
  .cover-v1 .heading {
    font-size: 3rem;
  }
}

.cover-v1 .blog-heading {
  font-weight: 900;
  color: #000;
  font-size: 4rem;
}

@media (max-width: 991.98px) {
  .cover-v1 .blog-heading {
    font-size: 2rem;
  }
}

.cover-v1 .post-meta {
  font-size: 18px;
}

.cover-v1 .subheading {
  font-size: 33px;
  color: #000;
}

@media (max-width: 991.98px) {
  .cover-v1 .subheading {
    font-size: 24px;
  }
}

.jarallax {
  position: relative;
  z-index: 0;
}

.jarallax > .jarallax-img {
  position: absolute;
  -o-object-fit: cover;
  object-fit: cover;
  /* support for plugin https://github.com/bfred-it/object-fit-images */
  font-family: "object-fit: cover;";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.mouse-wrap {
  position: relative;
  display: inline-block;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  bottom: 50px;
  z-index: 9;
}

.mouse-wrap:active,
.mouse-wrap:active > .mouse-label,
.mouse-wrap:focus,
.mouse-wrap:focus > .mouse-label {
  outline: none;
}

.mouse-wrap.mouse-with-slider {
  bottom: -50px;
}

.mouse-wrap .mouse-label {
  top: 0;
  color: #fff;
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
}

.mouse-wrap.dark .mouse-label {
  color: #000;
}

.mouse-wrap.dark .mouse {
  border: 2px solid #000;
}

.mouse-wrap.dark .mouse .scroll {
  background: #000;
}

.mouse-wrap .mouse {
  position: absolute;
  width: 22px;
  height: 42px;
  bottom: 30px;
  display: block;
  left: 50%;
  margin-left: -12px;
  border-radius: 15px;
  border: 2px solid #fff;
  -webkit-animation: intro 2s;
  animation: intro 2s;
}

.mouse-wrap .mouse .scroll {
  display: block;
  width: 3px;
  height: 3px;
  margin: 6px auto;
  border-radius: 4px;
  background: #fff;
  -webkit-animation: finger 2s infinite;
  animation: finger 2s infinite;
}

.gsap-reveal-hero {
  line-height: 0;
}

.gsap-reveal-hero .reveal-wrap {
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.gsap-reveal-hero .reveal-wrap .cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #d63447;
  margin-left: -100%;
  z-index: 2;
}

.gsap-reveal-hero .reveal-wrap .reveal-content {
  line-height: 1.5;
  z-index: 1;
  display: inline-block;
  -webkit-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  transform: translateX(-100%);
}
