.unslate_co--footer {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  color: #fff;
  left: 0;
  right: 0;
  bottom: 0px;
  z-index: 0;
  height: 400px;
}

.unslate_co--footer {
  text-align: center;
  font-weight: 300;
  color: rgba(0, 0, 0, 0.5);
}

.unslate_co--footer a {
  color: rgba(0, 0, 0, 0.6);
}

.unslate_co--footer a:hover {
  color: #000;
}

.unslate_co--footer .footer-site-logo {
  margin-bottom: 30px;
}

.unslate_co--footer .footer-site-logo a {
  font-size: 30px;
  font-weight: 900;
  font-family: "Raleway", sans-serif;
  color: #000;
}

.unslate_co--footer .footer-site-logo a span {
  color: #d63447;
}

.unslate_co--footer .footer-site-social {
  padding: 0;
  margin: 0;
  margin-bottom: 30px;
}

.unslate_co--footer .footer-site-social li {
  padding: 0;
  margin: 0;
  list-style: none;
  display: inline-block;
}

@media (max-width: 991.98px) {
  .unslate_co--footer .footer-site-social li {
    display: block;
  }
}

.unslate_co--footer .footer-site-social li a {
  display: inline-block;
  padding-left: 20px;
  padding-right: 20px;
  color: rgba(0, 0, 0, 0.5);
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
}

.unslate_co--footer .footer-site-social li a:hover,
.unslate_co--footer .footer-site-social li a:focus {
  color: #000;
}

@media (max-width: 991.98px) {
  .unslate_co--footer {
    height: auto !important;
    position: relative !important;
  }
}
