@import url("https://fonts.googleapis.com/css?family=Arimo:400|Raleway:300,400,700&display=swap");

body {
  font-family: "Raleway", sans-serif;
  color: #000;
  font-size: 16px;
  line-height: 30px;
  font-weight: 300;
  position: relative;
}

body:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  transition: 0.3s all ease;
  z-index: 8;
}

body.offcanvas {
  overflow: hidden;
}

body.offcanvas:before {
  visibility: visible;
  opacity: 1;
}

body,
.unslate_co--site-inner {
  background: #fff;
}

::-moz-selection {
  background: #000;
  color: #fff;
}

::selection {
  background: #000;
  color: #fff;
}

a {
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  transition: 0.3s all ease;
}

a,
a:hover {
  text-decoration: none !important;
}

.text-black {
  color: #000;
}

@media (max-width: 991.98px) {
  .unslate_co--site-inner {
    margin-bottom: auto !important;
  }
}

@media (max-width: 991.98px) {
  .unslate_co--footer {
    height: auto !important;
    position: relative !important;
  }
}

.btn {
  font-size: 11px;
  border-radius: 30px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 30px;
  padding-right: 30px;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  font-weight: 900;
}

.btn:active,
.btn:focus {
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.btn.btn-outline-pill.btn-custom-light {
  color: #000;
  border: 2px solid rgba(0, 0, 0, 0.5);
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  transition: 0.3s all ease;
}

.btn.btn-outline-pill.btn-custom-light:hover,
.btn.btn-outline-pill.btn-custom-light:active,
.btn.btn-outline-pill.btn-custom-light:focus {
  border-color: #000;
}

.btn.btn-outline-pill.btn-white {
  color: #fff;
  border: 2px solid rgba(255, 255, 255, 0.5);
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  transition: 0.3s all ease;
}

.btn.btn-outline-pill.btn-white:hover,
.btn.btn-outline-pill.btn-white:active,
.btn.btn-outline-pill.btn-white:focus {
  border-color: #fff;
}

.btn.btn-bg-black--hover:hover {
  background-color: #000;
  color: #000;
}

body,
.unslate_co--site-inner {
  background: #fff;
}
